html{height:100%}body{margin:0;font-family:-apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale;box-sizing:border-box;height:100%}a{color:inherit;text-decoration:none}*{box-sizing:inherit}#root{height:100%}code{font-family:source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace}

#game-root{height:calc(100% - 4rem)}

#start-root{height:100%;display:flex;flex:1 1;flex-direction:column;justify-content:center;text-align:center;padding-bottom:4rem}#start-select-div{font-size:1.2rem;margin-bottom:0.6rem}#start-select-div>label{display:block;margin-bottom:0.6rem;font-weight:bold}#start-select-div>select{width:8rem;height:3rem;outline:none;border:none;border-radius:4px;background:#495057;color:white;text-align:center;font-size:1rem;font-weight:bold}#start-btn-div>button{float:none;width:8rem;height:3rem;font-size:1.2rem}

.block{float:left;outline:none;margin:0;padding:0;text-align:center;font-size:30px;font-weight:bold;width:30px;height:30px;line-height:30px;border:none;cursor:pointer}.block.open-block{background:#e9ecef}.block.open-block.val-1{color:#1976d2}.block.open-block.val-2{color:#388e3c}.block.open-block.val-3{color:#d32f2f}.block.open-block.val-4{color:#7b1fa1}.block.open-block.val-5{color:#ffcd1a}.block.closed-block{background:#495057;color:#be3333}.block.closed-block:hover{background:#868e96}.block>img{width:30px;height:30px}.block.highlight{background:#868e96}

#board-root{display:flex;flex-direction:column;justify-content:center;align-items:center}#board-minNum-div{display:inline-block;width:100%;border-bottom:1px solid #e9ecef;margin-bottom:1rem}#board-minNum-div>h2{height:35px;text-align:center}#board-restart{height:7rem;margin-top:1rem}#board-restart>button{width:100%;height:3rem;font-size:1.2rem;color:#efefef}

